/* header
	----------------------------------- */
.header {
	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20px 15px 23px;
		@include rh(680) {
			padding: 5px 15px 5px; }
		@include sm {
			padding: 15px 10px 15px; }
		@include xs {
			flex-direction: column;
			padding: 10px;
			align-items: stretch; } }
	&__logo {
		flex-basis: 270px;
		@include sm {
			flex-basis: 200px; }
		@include xs {
			flex-basis: 75px;
			align-self: flex-start;
			padding-left: 5px; } }
	&__contact {
		margin-top: 15px;
		flex-basis: (770/1170 * 100%);
		@include sm {
			flex-basis: 72%; }
		@include xs {
			margin: 10px 0 0;
			align-items: center; } } }

/* contact
	----------------------------------- */
.contact {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	&__col {
		position: relative;
		padding-left: 55px;
		@include md {
			padding-left: 45px; }
		@include sm {
			padding-left: 40px; }
		@include xs {
			flex-basis: 50%;
			padding: 0 5px;
			text-align: center; }
		&::before {
			position: absolute;
			top: 7px;
			left: 0;
			z-index: 1;
			content: '';
			@include xs {
				display: none; } } }
	&__worktime {
		@include xs {
			display: none; } }
	&__worktime::before {
		@include s('clock'); }
	&__mainoff::before {
		@include s('geo');
		@include md {
			left: 10px; }
		@include xs {
			left: 0; } }
	&__call::before {
		@include s('phone');
		left: 20px;
		@include md {
			left: 10px; }
		@include xs {
			left: 0; } }
	&__mail::before {
		@include s('mail'); }

	&__title {
		@include f('tnr-r',20);
		line-height: 1.1;
		color: #414141;
		@include sm {
			font-size: 18px; }
		@include xs {
			font-size: 16px; } }
	&__descr {
		margin-top: 3px;
		font-size: 12px;
		color: #7a7a7a;
		br {
			@include xs {
				display: none; } } }
	&__mail {
		padding-left: 65px;
		@include md {
			padding-left: 55px; } }
	&__phone {
		display: block;
		margin-bottom: 10px;
		@include f('tnr-r',20,20);
		color: #414141;
		&:last-child {
			margin-bottom: 0; }
		@include sm {
			font-size: 17px; }
		@include xs {
			font-size: 15px; } } }

/* logo
	----------------------------------- */
.logo {
	display: flex;
	flex-direction: column;
	align-items: center;
	&__title {
		@include f('tnr-r',32);
		line-height: 1;
		color: #111;
		@include sm {
			font-size: 24px; }
		@include xs {
			font-size: 18px; } }
	&__descr {
		@include f('ub-l',16);
		line-height: 1.2;
		@include sm {
			font-size: 14px; }
		@include xs {
			font-size: 12px; } }
	&_sm {
		.logo__title {
			font-size: 24px;
			@include xs {
				font-size: 20px; } }
		.logo__descr {
			font-size: 12px; } } }

/* nav
	----------------------------------- */
.nav {
	padding: 15px 0;
	border-top: 1px solid rgba(#5facae, .3);
	@include rh(700) {
		padding: 5px 0; }
	@include sm {
		padding: 10px 0; }
	@include xs {
		position: absolute;
		height: 100vh;
		top: 90px;
		right: 0;
		left: 0;
		bottom: 0;
		display: flex;
		flex-direction: column;
		padding: 0;
		z-index: 10;
		background-color: #fff;
		opacity: 0;
		pointer-events: none;
		transform: scale(.7);
		transition: opacity .6s, transform .6s;
		&.is-active {
			opacity: 1;
			pointer-events: all;
			transform: scale(1); } }
	&__container {
		display: flex;
		justify-content: space-between;
		@include sm {
			margin: 0 -5px; }
		@include xs {
			flex-direction: column;
			align-items: center;
			flex-basis: 100%;
			justify-content: center; } }
	&__item {
		margin-left: 10px;
		@include sm {
			margin-left: 0; }
		@include xs {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; } }
		&.is-active .nav__link {
			color: $accent;
			box-shadow: 0 1px 0 $accent; } }
	&__link {
		padding: 2px 0;
		color: #303030;
		@include sm {
			font-size: 15px; }
		@include xs {
			@include f('ub-r',17); }
		&:hover {
			color: $accent;
			box-shadow: 0 1px 0 $accent; }
		&_call {
			color: $accent; }
		&_home {
			display: block;
			@include s('home');
			@include xs {
				background: none;
				width: auto;
				height: auto; }
			span {
				display: none;
				@include xs {
					display: block; } } } } }
/* screen
	----------------------------------- */
.screen {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
	border-top: 1px solid rgba(#5facae, .3);
	height: calc(100vh - 190px);
	min-height: 510px;
	@include rh(700) {
		min-height: calc(100vh - 137px); }
	@include sm {
		height: calc(100vh - 156px); }
	@include xs {
		height: auto;
		justify-content: flex-start; }
	&__container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: relative;
		max-width: 1158px;
		min-height: 450px;
		margin: -15px 0 0;
		width: 100%;
		z-index: 1;
		@include md {
			max-width: 920px; }
		@include sm {
			margin-top: -45px; }
		@include xs {
			margin: 0;
			padding: 15px 0 40px; } }
	&__pic {
		@include coverdiv;
		@include cover;
		z-index: 0;
		@include md {
			background-size: 145% 100%;
			background-position: 49% 50%; }
		@include sm {
			background-size: 226% 100%;
			background-position: 17% 50%; }
		@include xs {
			background-size: cover;
			background-position: 20% 50%; } }

	&__slider {
		width: 100%;
		padding: 0 30px;
		display: none;
		@include sm {
			max-width: 660px;
			margin: 0 auto; }
		@include xs {
			padding: 0; }
		&.slick-initialized {
			display: block; }
		.screen__arrow-next.slick-disabled,
		.screen__arrow-prev.slick-disabled {
			@include sm {
			    fill: rgba($accent, .8); }
			@include xs {
			    fill: rgba(#fff, .3); } }
		.screen__arrow-next,
		.screen__arrow-prev {
			@include sm {
				 	fill: darken($accent, 5); } }
		.screen__arrow-prev {
			@include xs {
				left: -7px; } }
		.screen__arrow-next {
			@include xs {
				right: -7px; } }
		.slick-arrow {
			@include xs {
				top: 27%;
				fill: #fff; }
			@include r(374) {
				top: 34.5%; } } }

	&__arrow {
		transition: fill .25s;
		background-color: transparent;
		fill: rgba($accent, .6);
		position: absolute;
		top: calc(50% - 23px);
		z-index: 2;
		&.slick-disabled {
			fill: rgba(#fff, .6);
			&:hover {
				fill: rgba(#fff, .6); } }
		&:hover {
			fill: $accent; }
		.arrow-icon {
			width: 42px;
			height: 45px; }
		&-prev {
			left: 0; }
		&-next {
			right: 0;
			.arrow-icon {
				@include rotate(180); } } }
	&__text {
		text-align: center;
		margin: 0 auto; }
	&__descr {
		@include xs {
			min-height: 48px;
			padding: 10px 30px;
			background-color: rgba($accent, .9);
			color: #fff;
			@include f('ub-r'); } }
	&__title {
		max-width: 600px;
		margin: 0 auto 15px;
		font-weight: 300;
		font-size: 32px;
		@include rh(700) {
			line-height: 1.2; }
		@include sm {
			font-size: 28px; }
		@include xs {
			padding: 0 10px;
			font-size: 20px;
			color: #000; } }
	&__form-wrap {
		margin: 55px 0 75px;
		@include rh(700) {
			margin: 15px 0; }
		@include xs {
			margin: 20px 0 0; } }
	&__form-inn {
		display: flex;
		justify-content: center;
		align-items: center;
		@include sm {
			flex-direction: column;
			align-items: stretch;
			padding: 0 100px; }
		@include xs {
			padding: 0 20px; } }
	&__form-field {
		flex-basis: (202/1068 * 100%);
		@include md {
			flex-basis: 25%; }
		@include sm {
			margin-bottom: 10px; }
		& + & {
			margin-left: 15px;
			@include sm {
				margin-left: 0; } } }
	&__btn {
		min-width: 256px;
		height: 45px;
		line-height: 45px;
		@include sm {
			width: 100%; } }
	&__adv {
		display: flex;
		justify-content: center;
		@include xs {
			margin-top: 10px;
			justify-content: space-between; }
		&-item {
			display: flex;
			align-items: center;
			justify-content: center;
			overflow: hidden;
			width: 118px;
			height: 118px;
			border-radius: 50%;
			border: 3px solid #ffffff;
			color: #fff;
			text-align: center;
			& + & {
				margin-left: 40px;
				@include xs {
					margin-left: 0; } } }
		&-inn {
			padding: 10px;
			height: 108px;
			width: 108px;
			border-radius: 50%;
			background-color: $accent;
			&_mod {
				padding-top: 30px;
				line-height: 1.1; } }
		&-count {
			@include f('bn-r',39);
			line-height: 30px; }
		&-text {
			font-size: 12px; }
		&-text_lg {
			font-size: 15px; } }
	&__icon {
		position: absolute;
		z-index: 1;
		border-radius: 50%;
		left: calc(50% - 14px);
		bottom: 17px;
		width: 28px;
		height: 28px;
		background-color: #fff;
		@include xs {
			bottom: 6px; }
		&::after {
			@include vcenter;
			content: '';
			border-radius: 50%;
			@include s('scroll'); } } }

/* services
	----------------------------------- */
.services {
	background-color: rgba(#f2e8df, .3);
	@include xs {
		padding: 20px 0 20px; }
	&__container {
		max-width: 1030px; }
	&__inn {
		display: flex;
		margin: 20px 0 45px;
		@include clr;
		@include rh(800) {
			margin: 15px 0 0; }
		@include rh(642) {
			margin: 5px 0 0; }
		@include md {
			max-width: 920px;
			margin: 15px auto; }
		@include xs {
			flex-wrap: wrap;
			margin-bottom: 0; } }
	&__item {
		float: left;
		display: flex;
		flex-direction: column;
		position: relative;
		width: 48.7%;
		padding: 30px 10px 10px 30px;
		border-radius: 10px;
		overflow: hidden;
		background: #f2e8df no-repeat 100% 0;
		z-index: 1;
		@include md {
			padding: 30px 10px 10px 20px; }
		@include sm {
			flex-basis: 47.6%;
			padding: 15px 10px; }
		@include xs {
			float: none;
			flex-basis: 100%;
			margin-bottom: 20px;
			padding: 15px;
			&:last-child {
				margin-bottom: 0; } }
		& + & {
			margin-left: 25px;
			@include md {
				margin-left: 15px; }
			@include sm {
				margin-left: 5px; } }
		&::after {
			content: '';
			@include coverdiv;
			z-index: -1;
			opacity: .2;
			background-repeat: no-repeat;
			background-position: 100% 100%; }
		&_bg-1::after {
			background-image: url(../img/bg/serv_bg-1.png); }
		&_bg-2::after {
			background-image: url(../img/bg/serv_bg-2.png); }
		&-title {
			margin-bottom: 20px;
			@include xs {
				margin-bottom: 10px; } } }
	&__list {
		padding-left: 25px;
		color: #000;
		@include rh(642) {
			margin-bottom: -50px !important; }
		@media screen and (max-width: 1023px), screen and (max-height: 800px) {
			margin: 0!important; }
		li {
			position: relative;
			font-size: 14px;
			margin-bottom: 3px;
			z-index: 1;
			&::before {
				position: absolute;
				left: -18px;
				top: 6px;
				content: '';
				@include s('arr-sm'); } } }
	&__btn {
		position: relative;
		z-index: 3;
		min-width: 186px;
		align-self: flex-end;
		margin-top: auto;
		@include xs {
			margin-top: 15px; } } }

/* benefits
	----------------------------------- */
.benefits {
	@include xs {
		display: none; }
	&__inn {
		margin: 30px 0 0;
		display: flex;
		justify-content: space-between;
		@include rh(642) {
			margin: 0; } }
	&__item {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		position: relative;
		min-height: 120px;
		flex-basis: 155px;
		line-height: 1.5;
		@include sm {
			flex-basis: auto; }
		&:first-child {
			margin-left: 0; }
		&:last-child {
			margin-right: 0; }
		&::before {
			display: block;
			margin: 0 auto;
			content: ''; }
		&:nth-child(1)::before {
			@include s('like'); }
		&:nth-child(2)::before {
			@include s('shield'); }
		&:nth-child(3)::before {
			@include s('cash'); }
		&:nth-child(4)::before {
			@include s('user'); }
		&:nth-child(5)::before {
			@include s('headphones'); } }

	&__descr {
		padding: 0 13px 0;
		font-size: 14px;
		color: #000;
		text-align: center; } }


/* feed
	----------------------------------- */
.feed {
	background-color: #f2e8df;
	background-image: repeating-linear-gradient(45deg, transparent 10px, transparent 20px, #efe3d8 20px, #efe3d8 30px, transparent 30px, transparent 40px, #efe3d8 40px, #efe3d8 50px, transparent 50px), repeating-linear-gradient(-45deg, transparent 10px, transparent 20px, #efe3d8 20px, #efe3d8 30px, transparent 30px, transparent 40px, #efe3d8 40px, #efe3d8 50px, transparent 50px);
	@include xs {
		padding: 25px 0; }
	&__container {
		max-width: 1158px; }
	&__slider {
		display: none;
		margin: 40px 0 0;
		padding: 0 105px;
		@include rh(700) {
			margin: 15px 0 0; }
		@include md {
			max-width: 920px;
			margin: 30px auto 0;
			padding: 30px; }
		@include sm {
			padding: 0 70px; }
		@include xs {
			padding: 0 15px; }
		&.slick-initialized {
			display: block; }
		.screen__arrow-next {
			@include md {
				right: -12px; }
			@include sm {
				right: 25px; }
			@include xs {
				right: -15px; } }
		.screen__arrow-prev {
			@include md {
				left: -13px; }
			@include sm {
				left: 25px; }
			@include xs {
				left: -15px; } } }
	&__pic {
		margin: 0 10px;
		position: relative;
		border: 2px solid #fff;
		@include md {
			margin: 0 2px; }
		@include xs {
			border: none; }
		&:hover .feed__controls {
			opacity: 1; } }
	&__img {
		width: 100%;
		margin: 0 auto;
		@include xs {
			width: auto;
			border: 2px solid #fff; } }
	&__controls {
		@include coverdiv;
		z-index: 1;
		font-size: 45px;
		background-color: rgba($accent, .7);
		opacity: 0;
		@include tr(opacity .25s);
		.icon-zoom {
			@include vcenter; }
		@include sm {
			display: none; } } }
/* clients
	----------------------------------- */
.clients {
	margin-top: 60px;
	@include rh(800) {
		margin-top: 25px; }
	&__slider {
		margin: 40px 0 0;
		padding: 0 70px;
		display: none;
		@include md {
			max-width: 930px;
			margin: 0 auto;
			padding: 0 30px; }
		@include xs {
			margin: 20px 0 0; }
		&.slick-initialized {
			display: block; }
		.slick-track {
			display: flex;
			align-items: center; } }
	&__img {
		margin: 0 auto; } }

/* about-company
	----------------------------------- */
.about-company {
	position: relative;
	background-color: rgba(234, 223, 220, 0.3);
	@include xs {
		padding: 20px 0; }
	&__container {
		@include md {
			max-width: 980px; }
		@include sm {
			padding: 0; } }
	&__background {
		@include coverdiv;
		background-size: 100% 100%;
		background-position: 50% 100%;
		background-repeat: no-repeat;
		z-index: -1;
		@include sm {
			background-position: 50% 50%;
			background-size: cover; } }
	&__descr {
		margin: 15px 15px 0;
		font-size: 16px;
		color: #525252;
		text-align: center;
		line-height: 1.3; }
	&__text {
		text-align: left;
		& + & {
			margin: 20px 0;
			@include rh(700) {
				margin: 10px 0; } } } }

/* specials
	----------------------------------- */
.specials {
	margin: 35px 0 0;
	@include rh(700) {
		margin: 15px 0 0; }
	&__container {
		max-width: 960px;
		margin: 0 auto;
		@include sm {
			margin: 0 -10px; } }
	&__slider {
		margin: 30px auto 25px;
		padding: 0 65px;
		@include rh(700) {
			margin: 15px auto 10px; }
		@include sm {
			padding: 0 40px; }
		@include xs {
			padding: 0 10px; }
		.screen__arrow-next {
			@include xs {
				right: -12px; } }
		.screen__arrow-prev {
			@include xs {
				left: -12px; } } }
	&__item {
		@include clr; }
	&__photo {
		float: left;
		width: 27%;
		padding: 0 15px;
		margin-right: 3%;
		@include sm {
			padding: 0; }
		@include xs {
			float: none;
			width: 100%;
			margin: 0 0 5px; }
		img {
			@include xs {
				margin: 0 auto; } } }
	&__data {
		float: left;
		width: 70%;
		@include xs {
			float: none;
			width: 100%;
			margin: 0 auto; } }
	&__name {
		padding: 5px 0 10px;
		@include f('ub-r',18);
		color: #000;
		line-height: 1.6;
		border-bottom: 1px solid #5facae;
		@include xs {
			padding: 0 10px;
			font-size: 17px;
			text-align: center; } }
	&__position {
		color: #525252;
		@include f('ub-r',16);
		@include xs {
			margin: 10px; } }
	&__merit {
		padding: 15px 0 0;
		@include xs {
			padding: 0; }
		&-item {
			position: relative;
			padding-left: 20px;
			margin: 13px 0 0;
			@include xs {
				margin: 5px 0 0;
				font-size: 15px; }
			&::before {
				position: absolute;
				left: 0;
				top: 9px;
				width: 8px;
				height: 8px;
				background-color: #b3d9da;
				border-radius: 50%;
				content: ''; } } } }

/* story
	----------------------------------- */
.story {
	background-image: repeating-linear-gradient(-45deg, #f8fdfa 0, #f8fdfa 10%, #fff 0, #fff 50%), repeating-linear-gradient(45deg, #f8fdfa 0, #f8fdfa 10%, #fff 0, #fff 50%);
	background-size: 26px 26px;
	background-blend-mode: multiply;
	background-color: #f9f5f4;
	@include xs {
		padding: 20px 0; }
	&__inn {
		max-width: 1072px;
		margin: 30px 0 30px 70px;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		@include rh(700) {
			margin: 5px 0 5px 70px; }
		@include md {
			margin: 30px; }
		@include xs {
			display: block;
			margin: 15px 0 0; } }
	&__col:nth-child(1) {
		flex-basis: (400/1072 * 100%);
		@include xs {
			float: left;
			width: 50%;
			margin: 0 15px 0 0;
			flex-basis: auto; } }
	&__col:nth-child(2) {
		flex-basis: (642/1072 * 100%);
		@include xs {
			width: auto;
			flex-basis: auto; } }
	&__text {
		@include sm {
			font-size: 14px; } }
	&__text + &__text {
		margin-top: 20px;
		@include xs {
			margin-top: 10px; } }
	&__img {
		width: 100%; } }

/* location
	----------------------------------- */
.location {
	position: relative;
	&__label {
		position: absolute;
		top: 0;
		left: 50%;
		z-index: 2;
		padding: 25px 20px 20px;
		max-width: 600px;
		width: 100%;
		transform: translateX(-50%);
		background-color: #fff;
		@include md {
			padding: 15px 0 5px;
			max-width: 400px; }
		@include xs {
			padding: 5px 0; } }
	&__title {
		margin: 20px 0 10px;
		@include md {
			margin: 10px 0; }
		@include xs {
			margin: 0; } }
	&__map-wrap {
		position: relative; }
	&__map {
		height: calc(100vh - 186px);
		@include sm {
			height: calc(100vh - 165px); }
		@include xs {
			height: 500px; } } }

/* footer
	----------------------------------- */
.footer {
	&__container {
		display: flex;
		justify-content: space-between;
		padding: 25px 15px 25px;
		@include sm {
			padding: 10px 10px 5px; }
		@include xs {
			padding: 10px 5px 5px;
			align-items: center; } }
	&__logo {
		margin-left: 55px;
		@include md {
			margin-left: 0; }
		@include sm {
			flex-basis: 230px; }
		@include xs {
			flex-basis: 165px; } }
	&__contact {
		flex-basis: 75%;
		margin-top: 15px;
		@include md {
			margin-top: 0;
			flex-basis: 78%; }
		@include sm {
			flex-basis: 62%;
			flex-wrap: wrap;
			justify-content: flex-end; }
		@include xs {
			flex-basis: 15%; }
		.contact__col {
			@include sm {
				flex-basis: 230px;
				padding-left: 55px; }
			@include xs {
				padding: 0; } }
		.contact__mainoff {
			@include xs {
				display: none; } } }
	&__copy {
		padding: 15px 0;
		border-top: 1px solid rgba(#626262, .3);
		font-size: 12px;
		color: #666666;
		line-height: (24/12);
		@include sm {
			padding: 5px 0; }
		@include xs {
			padding: 10px 0;
			line-height: 1.2; }
		&-text {
			@include xs {
				margin-bottom: 5px;
				text-align: center; } }
		&-container {
			display: flex;
			justify-content: space-between;
			max-width: 1200px;
			padding: 0 15px;
			margin: 0 auto;
			@include xs {
				flex-direction: column;
				align-items: center; } } } }


/* other styles
	----------------------------------- */
.section-icon {
	position: relative;
	max-width: 225px;
	margin: 0 auto;
	z-index: 2;
	&::after, &::before {
		position: absolute;
		top: 50%;
		z-index: 1;
		width: 77px;
		height: 1px;
		background-color: $accent;
		content: ''; }
	&::before {
		left: 0; }
	&::after {
		right: 0; }
	&__pic {
		position: relative;
		margin: 0 auto;
		width: 52px;
		height: 52px;
		border: 1px solid $accent;
		border-radius: 50%;
		&::after {
			@include vcenter;
			content: ''; }
		&_star::after {
			@include s('star'); }
		&_feed::after {
			@include s('feed'); }
		&_crown::after {
			@include s('crown'); }
		&_hammer::after {
			@include s('hammer'); }
		&_university::after {
			@include s('university'); }
		&_location::after {
			@include s('location'); } } }

